<template lang="pug">
    ul
        li.department__item-title Предметы:
        li.department__item-container(v-for="subject,idx in departmentSubjects") {{idx+1}}) {{subject.name}}
</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
    created() {
        this.fetchDepartmentSubjects(this.department.id)
    },
    props: {
        department: Object,
        subjects: Array
    },
    computed: {
        ...mapState ("departmentSubjects", {
            departmentSubjects: state => state.departmentSubjects
        }
        )
    },
    methods: {
        ...mapActions("departmentSubjects", ["fetchDepartmentSubjects"])
    }
}
</script>
<style scoped lang="scss">
.department__item-title {
    font-family: Alegreya Sans Bold;
    border-bottom: 1px dotted black;
}
</style>